.intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: "Rajdhani", sans-serif;
  font-size: 3rem;
  line-height: 1.7;
  min-height: 600px; }
  @media (max-width: 1000px) {
    .intro {
      font-size: 2.1rem;
      min-height: 900px; } }
  .intro__text {
    display: block;
    width: 60%;
    text-align: center; }
    @media (max-width: 1000px) {
      .intro__text {
        width: 80%; } }
    .intro__text--small {
      display: block;
      width: 90%;
      margin: 0 auto;
      font-size: 1.6rem; }
      @media (max-width: 1000px) {
        .intro__text--small {
          margin-top: 2vh; } }
  .intro__greeting {
    display: block;
    margin-bottom: 10px; }
